import React, { Component, useState, Suspense } from 'react';
import { Link, BrowserRouter as Router, Switch, Route, Redirect, useParams, useRouteMatch } from 'react-router-dom';
import { CSSTransition, TransitionGroup } from 'react-transition-group';


import Header from "./components/Header";
import FixedFooter from "./components/FixedFooter";
import Home from "./components/Home";
import About from "./components/About";
import Experience from "./components/Experience";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }
  render() {
    const { location } = this.props;
    return (
      <div className="App">
        <Router basename={process.env.PUBLIC_URL}>
          <Header></Header>
          <Route render={(location) => {
            // console.log(location.location);
            const { pathname, key } = location.location;
            return (
              <TransitionGroup component={null}>
                <CSSTransition key={key} timeout={500} mountOnEnter={false} unmountOnExit={true} classNames="slide-in">
                  <Switch>
                    <Route path="/experience" component={Experience} />
                    <Route path="/about" component={About} />
                    <Route path="/" component={Home} />
                  </Switch>
                </CSSTransition>
              </TransitionGroup>
            )
          }} />
          <FixedFooter></FixedFooter>
        </Router>
      </div>
    );
  }
}

export default App;
